import {scaleLinear, scaleOrdinal} from 'd3-scale';
import {area, curveMonotoneX, curveStepAfter} from 'd3-shape';
import {select} from 'd3-selection';
import {format} from 'd3-format';

export function viewHistogram(svg, data, samples, ext, options = {}) {
    svg = select(svg);
    svg.selectAll('*').remove();
    const range = [-1,1];
    const mid = (range[0] + range[1]) / 2;
    const rangeMid = [range[0], mid, range[1]];
    const opt = {...viewHistogram.options(), ...options};
    const f = format('+.' + opt.precision);
    const ft = d => f(d).replace('-', '&minus;').replace('+0', '0');
    const l = data[0].length - opt.overflow * 2;
    const x = scaleLinear().domain([0, l - 1]).range([opt.overflowWidth, opt.width - opt.overflowWidth]);
    const xv = scaleLinear().domain(range).range([opt.overflowWidth, opt.width - opt.overflowWidth]);
    const y = scaleLinear().domain([0, samples]).range([opt.height - opt.margin.top, opt.margin.bottom]);
    const z = scaleOrdinal().range(opt.colors);

    const areaShape = area()
        .curve(opt.curve ? curveMonotoneX : curveStepAfter)
        .x(function (d, i) {
            return x(i);
        })
        .y0(function (d) {
            return y(d[0]);
        })
        .y1(function (d) {
            return y(d[1]);
        });

    let prev;
    const stacked = data.map(function (series, seriesIndex) {
        return prev = series.map(function (value, valueIndex) {
            const previous = seriesIndex > 0 ? prev[valueIndex][1] : 0;
            return [previous, value + previous];
        });
    });

    const stacked2 = stacked.map((a) => {
        return a.splice(1, a.length - 2);
    });

    const bs = svg.selectAll('.bin').data(stacked2);
    const bin = bs
        .enter()
        .append('path')
        .attr('class', 'area')
        .style('fill', function (d, i) {
            return z(i);
        })
        .attr('d', function (d) {
            return areaShape(d);
        });

    svg.append('line')
        .attr('x1', Math.floor(x(0)))
        .attr('x2', Math.floor(x(0)))
        .attr('y1', y(0))
        .attr('y2', y(samples))
        .attr('stroke-width', 1)
        .attr('stroke-dasharray', '12 4')
        .attr('stroke', '#444');
    svg.append('line')
        .attr('x1', Math.ceil(x(data[0].length - 3)))
        .attr('x2', Math.ceil(x(data[0].length - 3)))
        .attr('y1', y(0))
        .attr('y2', y(samples))
        .attr('stroke-width', 1)
        .attr('stroke-dasharray', '12 4')
        .attr('stroke', '#444');
    svg.append('line')
        .attr('x1', x((data[0].length - 3) / 2))
        .attr('x2', x((data[0].length - 3) / 2))
        .attr('y1', y(0))
        .attr('y2', y(samples))
        .attr('stroke-width', 1)
        .attr('stroke-dasharray', '4 12')
        .attr('stroke', '#444');

    svg.selectAll('.labels').data(rangeMid)
        .enter()
        .append('text')
        .attr('text-anchor', 'middle')
        .attr('dominant-baseline', 'middle')
        .attr('font-family', opt.font)
        .attr('x', d => xv(d))
        .attr('y', opt.margin.top / 2)
        .html(d => ft(d));

    svg.selectAll('.extent').data([ext.min, ext.max])
        .enter()
        .append('text')
        .attr('text-anchor', 'middle')
        .attr('dominant-baseline', 'middle')
        .attr('font-family', opt.font)
        .attr('x', d => xv(Math.max(range[0], Math.min(range[1], d))))
        .attr('y', opt.height - opt.margin.bottom / 2)
        .html(d => ft(d));
}

viewHistogram.options = function () {
    return {
        width: 512,
        height: 512,
        margin: {
            top: 32,
            bottom: 32,
            left: 32,
            right: 32
        },
        overflowWidth: 32,
        overflow: true,
        curve: true,
        labels: true,
        precision: 2,
        colors: ['#777', '#ff2', '#f2f', '#2ff', '#f22', '#2f2', '#22f'],
        font: 'monospace'
    };
};
